@import '../settings';
@import '../common/typography';
@import '../common/colors';
@import '../common/button';
@import '../common/input';


    .video {
    position: relative;
    padding-bottom: 53%;
    padding-top: 30px; height: 0;
    }


.video iframe,
.video object,
.video embed {
position: absolute;
top: 15%;
left: 0;
right: 0;
bottom: 0;
width: 75%;
height: 75%;
margin: auto;
margin-top: 2%;
margin-bottom: 0%
}

@media screen and (max-width: 40.5em) {
  .video iframe,
  .video object,
  .video embed {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0%;
  }
}





